export class TreeHelisaConnect<T> {
    page: number;
    isLastPage: boolean;
    isUsed: boolean;
  
    constructor() {
      this.page = 0;
      this.isLastPage = false;
      this.isUsed = false;
    }          
  
    nextPage() {
      return this.page = this.page + 1;
    }
  }