import { Injectable } from '@angular/core';
import {BehaviorSubject, Observable, Subject} from "rxjs";
import {TotalTableHelisa} from "./table-helisa.interface";
import {TableHelisaComponent} from "./table-helisa.component";

export interface TableHelisaServiceInfo<T> {
  obj: T;
  table?: TableHelisaComponent<any>;
}

@Injectable({
  providedIn: 'root'
})
export class TableHelisaService<T> {

  private emitChangeSource = new Subject<TableHelisaServiceInfo<TotalTableHelisa>>();
  private emitNextPage = new Subject<TableHelisaServiceInfo<T[]>>();

  totalReturn = this.emitChangeSource.asObservable();
  nextPageReturn = this.emitNextPage.asObservable();

  setTotal(total: TotalTableHelisa, table?: TableHelisaComponent<T>) {
    this.emitChangeSource.next({obj: total, table: table});
  }

  addPage(page: T[], table?: TableHelisaComponent<T>) {
    this.emitNextPage.next({obj: page, table: table});
  }


  private emitVisibleButton$ = new Subject<boolean>();
  /**
   * Observable para saber si se debe mostrar o esconder el boton de add row
   */
  emitVisibleButton = this.emitVisibleButton$.asObservable();

  /**
   * para modificar el valor de si se muestra o no el boton de add row de la tabla
   * @param change indicar si se muestra o no el boton de add row de la tabla
   */
  changeVisibilityButton(change:boolean){
    this.emitVisibleButton$.next(change);
  }

}
