import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { AlertHelisaType } from './alert-helisa-type.enum';

@Component({
  selector: 'hel-alert',
  templateUrl: './alert-helisa.component.html',
  styleUrls: ['./alert-helisa.component.sass']
})
export class AlertHelisaComponent implements OnInit {

  content: string;
  title: String;
  hasCancel: boolean;

  constructor(public dialogRef: MatDialogRef<AlertHelisaComponent>,
    @Inject(MAT_DIALOG_DATA) public data) {
      this.content = data.content;
      this.title = data.title;
      this.hasCancel = data.type == AlertHelisaType.CONFIRMATION;
    }

  ngOnInit() {
  }

  onCancel(){
    this.dialogRef.close();
  }
}
