import { Component, OnInit, Input, Output, EventEmitter, ViewChild, AfterViewInit, ElementRef } from '@angular/core';
import { NestedTreeControl } from '@angular/cdk/tree';
import { MatTreeNestedDataSource, MatTree } from '@angular/material';
import { Node } from './node';
import { TreeHelisaService } from './tree-helisa.service';
import { Router } from '@angular/router';
import * as _ from 'lodash';
import { TreeHelisaConnect } from './tree-helisa-connect';
import { FormControl } from '@angular/forms';
import { take, reduce, first } from 'rxjs/operators';



export interface RequestTreeHelisa {
  page: number;  
}

@Component({
  selector: 'hel-tree',
  templateUrl: './tree-helisa.component.html',
  styleUrls: ['./tree-helisa.component.sass'],
  host:{
    '(document:keyup)': 'onKeyDown($event)'
  }
})
export class TreeHelisaComponent implements OnInit,AfterViewInit {

  //#region  ====== Variables =============
  private treeHelisaConnect: TreeHelisaConnect<Node>;
  formEdit:FormControl;
  @ViewChild('tree') tree:MatTree<any>;
  private selectedOptions = new Map<any, any>();
  /**
   * Datos del Arbol
   */
  @Input() data:Node;

  
  /**
   * Establece si se mostraran las opciones de 
   * Creacion, ediciÃ³n y eliminacion del nodo
   */
  @Input() showOptionsNode:boolean = true; 
  

  /**
   * Retorna el id del nodo removido
   */
  @Output() removed = new EventEmitter<number | string>();

  /**
   * Retorna un nodo editado
   */
  @Output() edited = new EventEmitter<Node>();

  /**
   * Retorna un nodo sin id del nodo , pero si con el parent
   * para conocer a cual fue aÃ±adido
   */
  @Output() added = new EventEmitter<Node>();
  @Output() collapseParent = new EventEmitter<boolean>();
  @Output() rangeScrolled = new EventEmitter<RequestTreeHelisa>();
  @Output() nodeSelected = new EventEmitter<number | string>();
  @Output() dobleClick = new EventEmitter<number | string>();
  @Output() keypressDelete = new EventEmitter<number | string | null>();
  @Output() keypressInsert = new EventEmitter<number | string | null>();
  @Output() checkedOptionNode = new EventEmitter<number | string | null>();
  @Output() uncheckedOptionNode = new EventEmitter<number | string | null>();
  
  treeControl = new NestedTreeControl<Node>(node => node.children);
  dataSource = new MatTreeNestedDataSource<Node>();


  isSingleClick: Boolean = true;  
  currentNode: Node = null;

  //#endregion ====== Variables ========

  constructor(private treeHelisaService:TreeHelisaService,
    private router:Router,
    private elementRef:ElementRef) {    
      
    //cargar datos pasados por el @Input
    if (!!this.data) {      
      let data = this.data;
      this.data = null;
      this.receivePage(data.children);
    } else {
      this.dataSource.data = [];     
      this.treeControl.dataNodes = []; 
    }
    
  }

  

  ngOnInit() {     
    // si se cargan datos por medio del servicio
    this.treeHelisaService.dataSourceObservable    
    .subscribe((res) => {            
      if(!!res && !!res.children){              
        this.receivePage(res.children)
      }else{
        this.dataSource.data = [];
        this.treeControl.dataNodes = [];
      }    
    });

    // Observable, si cambia el nodo seleccionado por medio del servicio
    this.treeHelisaService.nodeSelected
      .subscribe((res)=>{
        if(!!this.data && !!this.data.children)
        this.selectNode(this.data,res);
      })

    
      this.treeHelisaService.refreshTreeObservable
      .subscribe(res=>{
        this.refreshTree();
      })
      
      this.treeHelisaService.refreshTreeWithPaginationObservable
      .subscribe(res=>{
        this.refreshTreeWithPagination();
      })    
  }

  ngAfterViewInit() {     
    this.treeHelisaService.nodeExpand.subscribe(res=>{
      if(res != null){
        if(res){
          this.tree.treeControl.expandAll();
        }
      }
    })

    this.treeHelisaService.nodeCollapse.subscribe(res=>{
      if(res != null){
        if(res){
          this.tree.treeControl.collapseAll();
        }
      }
    })


    this.treeHelisaService.expandOneNodeObservable
      .subscribe(res => {        
        if (res != undefined) {          
          this.treeControl.expand(res);          
        }
      })

      this.treeHelisaService.collapseOneNodeObservable
      .subscribe(res => {        
        if (res != undefined) {
          this.treeControl.collapse(res);
        }
      })
  }


  //#region  ====== Events ===========
  onRedirect(node:Node){

    this.isSingleClick = true;
        setTimeout(()=>{
            if(this.isSingleClick){
              
              this.selectNode(this.data,node.id);    

              // if(!!node && !node.children){
              if(!!node){
                this.nodeSelected.emit(node.id);
                this.currentNode = node;
              }
            }
         },350)   
  }

  onScroll(event) {
    const element: HTMLDivElement = event.target;

    if ((element.offsetHeight + element.scrollTop) >= element.scrollHeight) {      
      this.goNextPage();
    }
  }

  onEdit(node:Node){  
    node.isEditable = true;
  }

  onAdd(node:Node){
    // si no tiene hijos instanciar el array
    if(!node.children){
      node.children = [];
    }
    node.children.push(
      {
        id: null,
        name: "",       
        isSelected: false,
        parent : node,
        isEditable : true
      }
    );     
    this.refreshTree();
  }

  onDelete(node:Node){ 
    // Remueve el nodo utilizando la libreria de lodash   
    _.remove(node.parent.children, node);
    
    this.refreshTree();
    this.removed.emit(node.id);
  }

  onEdited(node:Node,value:any){
    
      node.name = value;

      if(node.id == null && node.name == ""){              
        _.remove(node.parent.children, node);
        this.refreshTree();
      }
      else if(node.id && node.id != null && node.name.trim() !== ""){
        this.edited.emit(node);
        node.isEditable = false;
      }else if(!!node.id && node.id == null && node.name.trim() !== ""){
        this.added.emit(node);
        node.isEditable = false;
      }         
  }

  onCancel(node:Node,value:string){
    // Si no tiene id por ser un nuevo item, lo elimina
    if(node.id == null){
      _.remove(node.parent.children, node);
      this.refreshTree();  
    }

    node.isEditable = false; 
  }

  onDblClick(node:Node){
    this.isSingleClick = false;
    this.dobleClick.emit(node.id);
  }

  onKeyDown(event:KeyboardEvent){      
    switch (event.key) {
      case 'Delete':
        this.keypressDelete.emit((!!this.currentNode && this.currentNode.id)?this.currentNode.id:null)
        break;
      case 'Insert':
        this.keypressInsert.emit((!!this.currentNode && this.currentNode.id)?this.currentNode.id:null)
        break;
      case 'ArrowDown':        
        this.moveDownIntoTree();
        break;
      case 'ArrowUp':          
          this.moveUpIntoTree();
        break;     
    }
    
  }

  //#endregion ======= Events ========



  //#region  ======== Metodos =============

  private moveUpIntoTree(){
    if(!!this.data){
      // si aun no hay ningun node seleccionado selecciona el primero
      if(this.currentNode == null){
        this.selectNode(this.data,this.data.children[0].id);
        this.currentNode = this.data.children[0];
        if(!!this.currentNode.children && this.currentNode.children.length > 0){
          this.treeHelisaService.expandOneNode(this.currentNode);
        }
      }else{
        if(!!this.currentNode.parent && this.currentNode.id != null){
          // obtiene el indice del nodo seleccionado actualmente
          let index = this.currentNode.parent.children.indexOf(this.currentNode);
          
          if(this.currentNode.parent.id == null && index == 0){ 
            return 0;
          }else{// si tiene nodos al mismo nivel salta al nodo anterior
            if (index != undefined && index == 0) {
              this.currentNode = this.currentNode.parent;
              this.selectNode(this.data, this.currentNode.id);
              if (!!this.currentNode.children && this.currentNode.children.length > 0) {
                this.treeHelisaService.expandOneNode(this.currentNode);
              }
            } else { // si no tiene nodos al mismo nivel salta al nodo padre
              this.currentNode = this.currentNode.parent.children[index - 1];
              this.selectNode(this.data, this.currentNode.id);
              if (!!this.currentNode.children && this.currentNode.children.length > 0) {
                this.treeHelisaService.expandOneNode(this.currentNode);
              }
            } 
          }         
        }
      }
    }
  }

  private moveDownIntoTree(){      
    if(!!this.data){
      if(this.currentNode == null){
        this.selectNode(this.data,this.data.children[0].id);
        this.currentNode = this.data.children[0];
        if(!!this.currentNode.children && this.currentNode.children.length > 0){
          this.treeHelisaService.expandOneNode(this.currentNode);
        }
      }else{
        if(!!this.currentNode){
          // obtiene el indice del nodo seleccionado actualmente
          let index = (!!this.currentNode && !!this.currentNode.parent)?this.currentNode.parent.children.indexOf(this.currentNode): null;
          
          // si tiene childrens pasa al primer children
          if(!!this.currentNode.children  &&
            this.currentNode.children.length > 0){

            this.currentNode = this.currentNode.children[0];
            this.selectNode(this.data,this.currentNode.id);
            if(!!this.currentNode.children && this.currentNode.children.length > 0){
              this.treeHelisaService.expandOneNode(this.currentNode);
            }
          }
          // Si es el ultimo nodo del array pasa al nodo hermano del padre (tio) hacia abajo
          else if(index != undefined &&  
            index == this.currentNode.parent.children.length - 1 &&
            this.currentNode.parent.parent != null &&
            this.currentNode.parent.parent.children != null &&
            this.currentNode.parent.parent.children.length > 0){

            let indexOfParent = this.currentNode.parent.parent.children.indexOf(this.currentNode.parent); 
            this.currentNode = (this.currentNode.parent.parent.children[indexOfParent + 1] == undefined)? this.currentNode : this.currentNode.parent.parent.children[indexOfParent + 1];

            this.selectNode(this.data,this.currentNode.id);
            if(!!this.currentNode.children && this.currentNode.children.length > 0){
              this.treeHelisaService.expandOneNode(this.currentNode);
            }
          }else{ // si no tiene nodos al mismo nivel salta al siguiente hacia abajo
            this.currentNode = this.currentNode.parent.children[index + 1];
            this.selectNode(this.data,this.currentNode.id);
            if(!!this.currentNode.children && this.currentNode.children.length > 0){
              this.treeHelisaService.expandOneNode(this.currentNode);
            }
          }

        }
      }
    }
  }

  /**
   * Verifica si el nodo tiene hijos
   */
  hasChild = (_: number, node: Node) => !!node.children && node.children.length > 0;

  /**
   * Obtiene la descripcion completa del nodo
   * @example Nodo padre,nodo hijo,nodo nieto
   * @param node Debe tener todos los parent llenos hacia arriba
   */
  public static getDescription(node:Node):string{
      let result:string[]=[node.name];        
      let concat:string="";

      if(node.parent){          
        result.push(this.getDescription(node.parent))
      }                


      if(result.length == 1)
          return node.name;

      result = result.reverse();

      for (let i = 0; i < result.length; i++) {            
          let element = result[i];
          concat = concat + element + ((i == result.length-1)?"":",");                
      }
              
      return concat;
  }


  /**
   * Actualiza el arbol borrando toda la data , solo cuando no se utiliza paginacion
   */
  private refreshTree(){
    this.data = null;
    let _data = this.dataSource.data; 
    this.dataSource.data = null;
    this.dataSource.data = _data;
    this.treeControl.dataNodes = _data;
  }

  /**
   * Actualiza el arbol cuando se utiliza la paginacion (Cuando no , utilice el metodo refreshTree())    
   */
  private refreshTreeWithPagination(){    
    let _data = this.dataSource.data; 
    this.dataSource.data = null;
    this.dataSource.data = _data;
    this.treeControl.dataNodes = _data;
  }




  private goNextPage() {
    if (!this.treeHelisaConnect.isLastPage && !this.treeHelisaConnect.isUsed) {
      this.treeHelisaConnect.isUsed = true;
      this.rangeScrolled.emit({
        page: this.treeHelisaConnect.nextPage()              
      });
    }
  }

  private receivePage(data: Node[]) {    
    if(!this.data){ this.data = {id:null,name:"root",isSelected:false}}
    if (!this.data.children) { this.data.children = new Array<Node>(); this.treeHelisaConnect = new TreeHelisaConnect<Node>(); }    
    this.data.children = this.data.children.concat(data);
    this.data.children.forEach(node=>{
      this.fillParent(node,this.data);
    })

    this.dataSource.data = this.data.children;    
    this.treeControl.dataNodes = this.data.children;
    this.treeHelisaConnect.isLastPage = data.length === 0;
    this.treeHelisaConnect.isUsed = false;
  }

  /**
   * Llenan el campo parent de todos los nodos hijos
   * @param node 
   * @param parent 
   */
  private fillParent(node:Node,parent:Node){

    node.parent = parent;
    if(node.children && node.children.length > 0){
      node.children.forEach(item=>{
        this.fillParent(item,node);   
      })      
    }
  }


  /**
   * coloca como true del isSelected del nodo que concuerde con el id
   * @param node 
   * @param id 
   */
  private selectNode(node:Node,id:number|string){ 
      
      this.upSelectNode(node)      
      
      if(node.id == id){
        node.isSelected = true
        this.expandAllParents(node);
        return node;
      }else if (node.children != null){
           var i;
           var result = null;
           for(i=0; result == null && i < node.children.length; i++){
                result = this.selectNode(node.children[i], id);                
           }
           return result;
      }
      return null; 
  }

  private expandAllParents(node:Node){
    if(!!node && !!node.parent){
      this.treeHelisaService.expandOneNode(node.parent);
      this.expandAllParents(node.parent);
    }
  }

  /**
   * Elimina el isSelected de todos los nodos
   * @param node 
   */
  private upSelectNode(node:Node){
     node.isSelected = false;
     if(!!node.children)
     for(var i=0; i < node.children.length; i++){
         this.upSelectNode(node.children[i]);                
    }
  }

  getClassNode(node: Node): string[]{
    let classNode = [];
    if(node.isSelected) {
      classNode.push("isSelected");
    }
    if(node.classNode){
      classNode.push(node.classNode);
    }
    return classNode;
  }

  onEditMode(node, editMode) {
    this.getSelectedOptions(node).editMode = editMode;
  }

  onSelectOption(event, node) {
    node.isCheckedOption = event.source.selected;
    if (node.isCheckedOption)
      this.checkedOptionNode.emit(node.id);
    else
      this.uncheckedOptionNode.emit(node.id);
  }

  getSelectedOptions(node: Node): any {
    if (this.selectedOptions.has(node.id))
      this.reloadSelectedOptions(node, this.selectedOptions.get(node.id).editMode);
    else
      this.reloadSelectedOptions(node, false);
    return this.selectedOptions.get(node.id);
  }

  private reloadSelectedOptions(node: Node, editMode: boolean) {
    const array = new Array();
    node.options.forEach(option => {
      if (option.isCheckedOption)
        array.push(option.id);
    });
    const obj = {formControl: new FormControl(array), editMode: editMode};
    this.selectedOptions.set(node.id, obj);
  }

  //#endregion ====== Metodos ============
}
