import {SortDirection} from "@angular/material";

export interface ColumnConfig {
  name: string;
  title: string;
  visible?: boolean;
  sortable?: boolean;
  groupable?: boolean;
  totalType?: TotalType;
  searchable?: boolean;
  sortDirection?: SortDirection;
}

export interface AddRowButton{  
  text: string;
  showButton:boolean;
}

export enum TotalType {
  SUM, AVERAGE, COUNT
}

export enum ChangeColumnConfigurationType {
  SORT, UNKNOWN, TOTAL
}

export interface EventColumn {
  column: ColumnConfig;
  columnConfigurations: Array<ColumnConfig>;
  type: ChangeColumnConfigurationType;
}

export interface TotalTableHelisa {
  column: ColumnConfig;
  value: number;
}

export interface TotalGroup {
  sum: number;
  count: number;
}

export interface EventSearch {
  text: string;
  columnConfigurations: Array<ColumnConfig>;
}

export interface RequestTableHelisa {
  page: number;
  body: any;
}

export interface DropElement<T>{
 value: T;
 order: number;
}

export enum TableHelisaType {
  REMOTE, LOCAL
}


export interface Cell {
  column: ColumnConfig;
  row: any;
 }

 export interface ConfigCellStyles{
   cellData: any;
   classCell: string 
 }

 export interface ConfigRowStyles{
  column: ColumnConfig;
  data: any,
  classRow: string 
}

//@dynamic
export abstract class ColumnConfigUtil {
  static getValue(obj: any, column: ColumnConfig): any {
    return column.name.split('.').reduce((o, field) => o && o[field], obj);
  }
}
