import { Injectable } from '@angular/core';
import { BehaviorSubject } from "rxjs";
import { OptionAutocompleteHelisa } from './autocomplete-helisa.component';

@Injectable()
export class AutocompleteHelisaService<T> {

  private emitChangeSource = new BehaviorSubject<Array<OptionAutocompleteHelisa<T>>>([]);
  public dataSource$ = this.emitChangeSource.asObservable();

  constructor() { }

  setDataSource(options: Array<OptionAutocompleteHelisa<T>>) {
    this.emitChangeSource.next(options);
  }

}
