import { Injectable } from '@angular/core';
import { Subject, BehaviorSubject, Subscriber } from 'rxjs';
import { Node } from './node';


@Injectable({
    providedIn: 'root'
})
export class TreeHelisaService {

    // Observable string sources
    private emitNodeSelected = new BehaviorSubject<number>(1);    
    // Observable string streams
    nodeSelected = this.emitNodeSelected.asObservable();
    // Service message commands
    changeNodeSelected(idResidentialArea: number) {
        this.emitNodeSelected.next(idResidentialArea);
    }



    // Observable string sources
    private emitDataSource = new BehaviorSubject<Node>(undefined);    
    // Observable string streams
    dataSourceObservable = this.emitDataSource.asObservable();
    // Service message commands
    changeDataSource(data:Node) {
        this.emitDataSource.next(data);
    }


      
    // Expand node observable
      private emitExpandAllNodes = new BehaviorSubject<boolean>(null);          
      nodeExpand = this.emitExpandAllNodes.asObservable();      
      expandAllNodes(expand:boolean) {
          this.emitExpandAllNodes.next(expand);
      }


      // Collapse node observable
      private emitCollapseAllNodes = new BehaviorSubject<boolean>(null);          
      nodeCollapse = this.emitCollapseAllNodes.asObservable();      
      collapseAllNodes(collapse:boolean) {
          this.emitCollapseAllNodes.next(collapse);
      }


      private emitRefreshTree = new Subject<void>();
      refreshTreeObservable = this.emitRefreshTree.asObservable();
      refreshTree(){
          this.emitRefreshTree.next();
      }

      private emitRefreshTreeWithPagination = new Subject<void>();
      refreshTreeWithPaginationObservable = this.emitRefreshTreeWithPagination.asObservable();
      refreshTreeWithPagination(){
          this.emitRefreshTreeWithPagination.next();
      }



      private emitExpandOneNode = new Subject<Node>();
      expandOneNodeObservable = this.emitExpandOneNode.asObservable();
      expandOneNode(node:Node){
        this.emitExpandOneNode.next(node);
      }

      private emitCollapseOneNode = new Subject<Node>();
      collapseOneNodeObservable = this.emitCollapseOneNode.asObservable();
      collapseOneNode(node:Node){
        this.emitCollapseOneNode.next(node);
      }

    constructor(){
        
    }
  
}