import {Component, EventEmitter, Input, OnInit, Output, ViewChild, ElementRef} from '@angular/core';
import {FormControl} from "@angular/forms";

@Component({
  selector: 'hel-input',
  templateUrl: './input-helisa.component.html',
  styleUrls: ['./input-helisa.component.sass']
})
export class InputHelisaComponent implements OnInit {

  @Input() placeholder = '';
  @Output() setValue: EventEmitter<string> = new EventEmitter<string>();
  @Input() isSearch = false;
  @Input() inputFormControl: FormControl = new FormControl('');
  @Input() isFocused = false;

  @ViewChild('inputText') nameInput: ElementRef;

  constructor() { }

  ngOnInit() {
    if (this.isFocused) {
      this.nameInput.nativeElement.focus();
    }
  }

  search() {
    this.setValue.emit(this.inputFormControl.value);
  }

}
